// features/cart/cartSlice.ts
import { createSlice, PayloadAction } from "@reduxjs/toolkit";

interface CartState {
  courseIds: number[];
}

const initialState: CartState = {
  courseIds: [],
};

const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    addCourseToCart: (state, action: PayloadAction<number>) => {
      if (!state.courseIds.includes(action.payload)) {
        state.courseIds.push(action.payload);
      }
    },
    removeCourseFromCart: (state, action: PayloadAction<number>) => {
      state.courseIds = state.courseIds.filter((id) => id !== action.payload);
    },
    clearCart: (state) => {
      state.courseIds = [];
    },
    setCart: (state, action: PayloadAction<number[]>) => {
      state.courseIds = action.payload; // khởi tạo giỏ hàng
    },
  },
});

export const { addCourseToCart, removeCourseFromCart, clearCart, setCart } =
  cartSlice.actions;
export default cartSlice.reducer;
