import { createAsyncThunk } from "@reduxjs/toolkit";
import { setCart } from "../cart/cartSlice";
import { da } from "zod/v4/locales";

// ---- LOGIN ----
export const loginUserThunk = createAsyncThunk(
  "auth/login",
  async (
    credentials: { email: string; password: string },
    { rejectWithValue, dispatch }
  ) => {
    try {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(credentials),
      });

      if (!res.ok) {
        const error = await res.json().catch(() => ({}));
        return rejectWithValue(error.message || "Sai tài khoản hoặc mật khẩu");
      }

      const data = await res.json();

      console.log(data);
      // --- LẤY GIỎ HÀNG CỦA USER SAU KHI LOGIN ---
      try {
        const cartRes = await fetch(
          `${process.env.NEXT_PUBLIC_API_URL}/cart/user/${data.user.id}`,
          {
            headers: { Authorization: `Bearer ${data.token}` },
          }
        );
        const cartData = await cartRes.json();
        console.log(cartData);
        const items = Array.isArray(cartData?.cart_items)
          ? cartData.cart_items
          : [];
        // Lưu id các khóa học vào redux
        dispatch(setCart(items.map((i: any) => i.course_id)));
      } catch (err) {
        console.error("Lỗi lấy giỏ hàng:", err);
      }

      return await data.token;
    } catch (err: any) {
      return rejectWithValue(err.message);
    }
  }
);

// ---- LOGOUT ----
export const logoutThunk = createAsyncThunk("auth/logout", async () => {
  localStorage.removeItem("token");
});
